<template>
	<view class='container padding'>
		<view class="title">反馈详情</view>
		<view class="info">
			<!-- 客户反馈才关联订单，员工反馈无 -->
			<view class="row flex-row-between" v-if="feedbackInfo.carTypeName">
				<view class="key">关联订单</view>
				<view class='value flex-row-between text-ellipsis'>
					<text class='txt text-ellipsis'>{{feedbackInfo.carTypeName}}</text>
					<text class='txt text-ellipsis'>{{feedbackInfo.store4sName}}</text>
				</view>
			</view>
			<view class="row flex-row-between">
			<view class="key">问题类型</view><view class='value'>{{feedbackInfo.feedbackTypeStr}}</view>
			</view>
			<view class="row flex-row-between-start">
				<view class="key">投诉/建议</view><view class='value result'>{{feedbackInfo.problem}}</view>
			</view>
			<!-- 新增 -->
			
			<view class="row flex-row-between">
				<view class="key">反馈时间</view><view class='value'>{{feedbackInfo.createTimeStr}}</view>
			</view>
			<view class="row flex-row-between">
				<view class="key">反馈状态</view><view class='value'>{{feedbackInfo.state==0?'待处理':'已处理'}}</view>
			</view>
			<view class="row flex-row-start-start" v-if="affixList.length>0">
				<view class="key">凭证预览</view>
				<view class='value'>
					<view class="upload_w">
						<view class="upload">
							<view class="item" v-for='(litt,idx) in affixList' :key='idx'>
								<view @click='lookAffix(litt)' class='inner_w'>
									<image :src="litt" class='pic'/>
								</view>
							</view>
						</view>
					</view>
				</view>
			</view>
		</view>
		
		<!-- 反馈人信息 -->
		<block>
			<view class="title">反馈人信息</view>
			<view class="info">
				<view class="row flex-row-between">
					<view class="key">姓名</view><view class='value'>{{feedbackInfo.userName}}</view>
				</view>
				<view class="row flex-row-between">
					<view class="key">联系电话</view><view class='value'>{{feedbackInfo.userMobile}}</view>
				</view>
				<view class="row flex-row-between">
					<view class="key">反馈人身份</view><view class='value'>{{feedbackInfo.userTypeStr}}</view>
				</view>
			</view>
		</block>
		
		<!-- 项目组管理员 课件-->
		<block v-if='adminRole==2'>
			<view class="title">党支部信息</view>
			<view class="info">
				<view class="row flex-row-between">
					<view class="key">管理员姓名</view><view class='value'>{{feedbackInfo.partyHandler?feedbackInfo.partyHandler:''}}</view>
				</view>
				<view class="row flex-row-between">
					<view class="key">管理员电话</view><view class='value'>
						{{feedbackInfo.partyMobile?feedbackInfo.partyMobile:''}}
					</view>
				</view>
				<view class="row flex-row-between">
					<view class="key">党支部名称</view>
					<view class='value'>{{feedbackInfo.partyDeptName?feedbackInfo.partyDeptName:''}}</view>
				</view>
			</view>
		</block>
		
		<!-- 此备注就是 党支部上报问题的问题描述 -->
		<block v-if="adminRole==2">
			<view class="title">备注</view>
			<view class="info flex-row-between">
				<textarea 
					class="textarea" 
					placeholder="请输入答复内容"
					maxlength="500" 
					auto-height
					disabled
					v-model="feedbackInfo.remark"
					v-if="!questionDialog&&!projectQuestion"
					>
				</textarea>
			</view>
		</block>
		
		
		<view class="title">处理反馈</view>
		
		<block v-if="adminRole==2 && feedbackInfo.nodeStep>=30">
			<view class="opation_control flex-row-start">
				<view class="item flex-row-start" v-if="feedbackInfo.nodeStep>=30">
					<text class='diot'></text>
					<text class='txt'>已上报业务部门</text>
				</view>
				<view class="item flex-row-start" v-if="feedbackInfo.nodeStep>=40">
					<text class='diot'></text>
					<text class='txt'>已上报公司决策</text>
				</view>
			</view>
		</block>
		<!-- 处理反馈 -->
		<view class="info flex-row-between">
			<textarea 
				class="textarea"
				placeholder="请输入答复内容" 
				maxlength="500"
				auto-height
				:show-confirm-bar="false"
				v-model="paramsInfo.reply"
				v-if="!questionDialog&&!projectQuestion"
			></textarea>
			<text class="max">{{paramsInfo.reply.length}}/500</text>
		</view>
		
		<view class='btn_wrapper flex-row-between'>
			<view class="report" v-if="feedbackInfo.nodeStep<40">
				<button class="cu-btn block" @click="upLoadQuestion">
					上报问题
				</button>
			</view>
			<view class="sure">
				<button class="cu-btn block" @click="makeSureRepeat" :disabled="btnDisabled">
					确认回复
				</button>
			</view>
		</view>
		
		<!-- 党支部问题 弹框-->
		<view class="cu-modal question_dialog" :class="questionDialog?'show':''" catchtouchmove="return">
			<view class="cu-dialog padding bg-white">
				<view class="close" @click="questionDialog=false">✕</view>
				<view class="content text-left">
					<view class="title">上报问题处理中心</view>
					<view class="info dialog_info">
						<textarea 
							class="textarea dialog_area" 
							placeholder="请输入备注内容" 
							maxlength="500" 
							:show-confirm-bar="false"
							auto-height
							v-model="reportParams.remark"
							v-if="questionDialog"
							:fixed='true'
						></textarea>
						<text class="max">{{reportParams.remark.length}}/500</text>
					</view>
				</view>
				<view class="action margin-0 flex-sub" @click="dzbReport">
					<button class="cu-btn make_sure">提交</button>
				</view>
			</view>
		</view>
		
		
		<!-- 项目组上报 问题-->
		<view class="cu-modal upload_dialog" :class="projectQuestion?'show':''" catchtouchmove="return">
			<view class="cu-dialog padding bg-white">
				<view class="close" @click="projectQuestion=false">✕</view>
				<view class="content text-left">
					<view class="title">上报问题</view>
					<view style="margin-bottom:50rpx">
						<view class="flex-row-center radio_group">
							<view class="radio_item flex-row-start" @click="switchStep(1)">
								<image 
									src="../../static/select.png" 
									:class="[feedbackInfo.nodeStep>=30?'opacity':'','img']"
									v-if="companyOne"
								></image>
								<image src="../../static/unselect.png" v-else ></image>
								<text>{{feedbackInfo.nodeStep>=30?'已上报业务部门':'上报业务部门'}}</text>
							</view>
							<view class="radio_item flex-row-start" @click="switchStep(2)">
								<image 
									src="../../static/select.png" 
									v-if="companyTwo"
									:class="[feedbackInfo.nodeStep>=40?'opacity':'','img']"
								></image>
								<image src="../../static/unselect.png" v-else ></image>
								<text>{{feedbackInfo.nodeStep>=40?'已上报公司决策':'上报公司决策'}}</text>
							</view>
						</view>
					</view>
				</view>
				<view class="action margin-0 flex-sub" @click="reportCompany">
					<button class="cu-btn make_sure">提交</button>
				</view>
			</view>
		</view>
		
		
		<!-- 图片查看 -->
		<view class="cu-modal" :class="lookImg?'show':''" catchtouchmove="return">
			<view class="cu-dialog">
				<view class="bg-img">
					<image :src="currentImgUrl" class="dialogImg" mode="widthFix"></image>
				</view>
				<view class="cu-bar bg-white">
					<view class="action margin-0 flex-sub" @click="lookImg=false">关闭</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	import {formatTime,http2Https} from "../../common/tools.js"
	export default {
		data() {
			return {
				text:'',
				questionDialog:false, // 党支部上报问题
				projectQuestion:false,//项目组上报问题
				fromType:1 ,//1,党支部，2.项目组
				typeStep:2,
				companyOne:true, //业务部门
				companyTwo:false, //决策部门
				paramsInfo:{ //回复参数
					feedBackId:'',
					reply:'',
				},
				reportParams:{//上报参数
					remark:'',
					feedBackId:'',
					nodeStep:''
				},
				feedbackInfo:{},
				affixList:[],
				adminRole:'',// 1-党支部管理员，2-项目组管理员
				lookImg:false,
				currentImgUrl:''
			};
		},
		
		methods:{
			switchStep(type){
				if(type==1){ //上报公司决策
					if(this.feedbackInfo.nodeStep<30){ // 还没上报 可以点
						this.companyOne=!this.companyOne
					}else{ //上报了，不可点击
						this.companyOne=true
					}
				}else if(type==2){
					if(this.feedbackInfo.nodeStep>=30 && this.feedbackInfo.nodeStep<40){ // 还没上报 可以点
						this.companyTwo=!this.companyTwo
					}
				}
			},
			reportCompany(){
				delete this.reportParams.remark
				if(this.feedbackInfo.nodeStep<30 && !this.companyOne){
					uni.showToast({title:'请勾选上报业务部门受理',icon:"none"})
					return
				}
				if(this.feedbackInfo.nodeStep>=30 &&this.feedbackInfo.nodeStep<40 && !this.companyTwo){
					uni.showToast({title:'请勾选上报公司决策',icon:"none"})
					return
				}
				this.reportParams.nodeStep=this.feedbackInfo.nodeStep+10
				this.request("/feedBack/reportFeedBack",this.reportParams).then((res)=>{
					if(res.resultCode==0){
						uni.showToast({title:res.resultMsg,icon:"none"})
						this.projectQuestion=false
						this.getInfo()
					}else{
						uni.showToast({title:res.resultMsg?res.resultMsg:'网络异常请稍后重试',icon:"none"})
					}
				})
			},
			upLoadQuestion(){
				if(this.adminRole==1){ //党支部
					this.questionDialog=true
				}else{ //项目组
					this.projectQuestion=true
				}
			},
			dzbReport(){
				if(!this.reportParams.remark || this.reportParams.remark.length<10){
					uni.showToast({title:"请填写不少于10字备注内容",icon:"none"})
					return
				}
				this.reportParams.nodeStep=20 //党支部上报20
				this.request("/feedBack/reportFeedBack",this.reportParams).then((res)=>{
					if(res.resultCode==0){
						uni.showToast({title:res.resultMsg,icon:"none",success:()=>{
							setTimeout(()=>{
								uni.navigateBack({
									delta:1
								})
							},1500)
						}})
					}else{
						uni.showToast({title:res.resultMsg?res.resultMsg:'网络异常请稍后重试',icon:"none"})
					}
				})
			},
			getInfo(){
				this.request("/feedBack/detail",this.paramsInfo).then((res)=>{
					if(res.resultCode==0){
						var _data=res.data
						switch(_data.feedbackType){
							case 1 : _data.feedbackTypeStr="信息咨询类";break
							case 2 : _data.feedbackTypeStr="改善建议类";break
							case 3 : _data.feedbackTypeStr="服务投诉类";break
							case 4 : _data.feedbackTypeStr="质量投诉类";break
							case 5 : _data.feedbackTypeStr="违规违纪类";break
							default: _data.feedbackTypeStr="- -"
						}
						_data.createTimeStr=formatTime(_data.createTime)
						
						if(_data.attUrls){
							this.affixList=_data.attUrls.split(',')
							this.affixList.forEach((item)=>{
								item=http2Https(item)
							})
						}
						_data.nodeLogList.forEach((item)=>{
							switch(item.nodeStep){
								case 10 : item.nodeStepStr="党支部受理";break
								case 20 : item.nodeStepStr="问题处理中心受理";break
								case 30 : item.nodeStepStr="业务部门受理";break
								case 40 : item.nodeStepStr="上报公司决策";break
							}
							item.nodeDateStr=formatTime(item.nodeDate)
						})
						
						// 判断用户类型
						switch(_data.userType){
							case 0 :_data.userTypeStr="用户";break
							case 1:_data.userTypeStr="北汽员工";break
							case 2 :_data.userTypeStr="4S店员工";break
							default :_data.userTypeStr="用户";
						}
						this.feedbackInfo=_data
					}else{
						uni.showToast({title:res.resultMsg?res.resultMsg:'网络异常,请稍后重试',icon:'none'})
					}
				})
			},
			makeSureRepeat(){
				if(!this.paramsInfo.reply || this.paramsInfo.reply.length<10){
					uni.showToast({title:"请填写不少于10字的处理反馈",icon:"none"})
					return
				}
				uni.showModal({
					title:"提示",
					content:"是否确认处理此问题?",
					success:(res)=>{
						if(res.confirm){
							this.request("/feedBack/replyFeedBack",this.paramsInfo).then((res)=>{
								if(res.resultCode==0){
									uni.showToast({title:res.resultMsg,icon:"none",success:()=>{
										setTimeout(()=>{
											uni.navigateBack({
												delta:1
											})
										},1500)
									}})
								}else{
									uni.showToast({title:res.resultMsg?res.resultMsg:'网络异常请稍后重试',icon:"none"})
								}
							})
						}
					}
				})
			},
			lookAffix(item){
				if(this.mobileModel=="VOG-AL00"){ //华为p30
					this.currentImgUrl=item;
					this.lookImg=true
				}else{
					//获取 url数组
					var _urlList=[]
					this.affixList.forEach((item)=>{
						_urlList.push(item)
					})
					uni.showLoading()
					uni.previewImage({
						current:item,
						urls: _urlList,
						longPressActions:{
							itemList: ['发送给朋友', '保存图片', '收藏']
						},
						fail(err){
							this.currentImgUrl=item;
							this.lookImg=true
						},
						complete(){
							uni.hideLoading()
						}
					})
				}
			}
		},
		onLoad(params){
			this.paramsInfo.feedBackId=params.feedBackId
			this.reportParams.feedBackId=params.feedBackId
			this.getInfo()
			let _userInfo=JSON.parse(uni.getStorageSync('userInfo'));
			this.adminRole=_userInfo.role
			console.log(this.adminRole)
		}
	}
</script>

<style lang="stylus">
	.container
		padding-bottom:20upx
		.title
			height:44upx;
			font-size:32upx;
			font-weight:600;
			color:rgba(30,30,30,1);
			margin-bottom:45upx
			&:not(:first-child)
				margin-top:60upx
			.little
				color:#8491A8
				font-size:24upx
				font-weight:normal
		.opation_control
			margin-bottom:30upx
			.item
				flex:1
				.diot
					width:23upx;
					height:23upx;
					background:rgba(216,152,100,1);
					border-radius:6upx;
					margin-right:15upx
				.txt
					font-size:28upx;
					font-weight:bold;
					color:rgba(30,30,30,1);
		.info
			min-height:210upx;
			background:rgba(248,249,252,1);
			border-radius:8upx;
			padding:30upx 30upx
			position:relative
			&.dialog_info
				max-height:500upx
				overflow-y scroll
			.row
				&:not(:first-child)
					margin-top:40upx
				.key
					flex-basis:226upx
				.value
					width:400upx
					&.result
						word-break:break-word
					.txt
						flex:1
			.textarea
				width:100%
				font-size:28upx
				min-height:150upx
				padding-bottom:20upx
				&.dialog_area
					max-height:400upx
					overflow:scroll
			.max
				position:absolute
				bottom:10upx
				right:10upx
				color:rgba(178,182,192,1)
				font-size:24upx
		.upload_w
			.upload
				display:flex
				color:#1E1E1E
				flex-direction:row
				justify-content flex-start
				flex-wrap:wrap
				.item
					width:130upx
					height:130upx;
					background:rgba(249,251,252,1);
					border:2upx solid rgba(227,233,240,1);
					margin-bottom:25upx
					display:flex
					flex-direction:row
					align-items:center
					justify-content center
					position:relative
					.inner_w
						width:100%
						height:100%
						display:flex
						flex-direction:row
						align-items:center
						justify-content center
						.img
							width:48upx
							height:48upx
						.pic
							width:100%
							height:100%
					&:not(:last-child)
						margin-right:4%
					&.inner
						display:flex
						flex-direction row
						align-items center
						image
							width:48upx
							height:48upx
		.btn_wrapper
			background:#FFFFFF
			height:155upx
			margin-top:60upx
			.cu-btn
				height:112upx
			.sure
				flex:1
				button
					border-radius:16upx;
					color:#fff
					background:rgba(31,35,52,1);
			.report
				width:280upx
				margin-right:10upx
				button
					border-radius:16upx;
					border:2upx solid rgba(203,210,219,1);
					color:#1F2334
					background:#fff
		.question_dialog,.upload_dialog
			z-index:99
			.cu-dialog
				width:600upx
				position:relative
				.close
					position:absolute
					padding:10upx
					font-size:28upx
					color:#1E1E1E
					right:15upx
					top:15upx
				.title
					font-size:36upx;
					font-family:PingFangSC-Medium,PingFang SC;
					font-weight:500;
					color:rgba(31,35,52,1);
				.radio_group
					.radio_item
						flex-basis:50%
						margin-left:0upx
						.img
							&.opacity
								opacity:.7
						text
							flex:1
							font-size:28upx
							text-align:left
			.make_sure
				height:110upx;
				background:rgba(31,35,52,1);
				border-radius:16upx;
				font-size:28upx;
				letter-spacing 2upx
				font-family:PingFangSC-Medium,PingFang SC;
				font-weight:500;
				color:rgba(255,255,255,1);
				width:100%
				margin-top:30upx			
</style>
